@import url("https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,600;1,300;1,400&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.footer__three__images .footer__facebook {
  margin-right: 30px;
}
.footer__three__images .footer__twitter {
  margin-right: 30px;
}

.header__main {
  background-image: url(/images/bg-header-desktop.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.header__column__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50px;
}

.two__buttons {
  display: flex;
  justify-content: center;
}

.symbol__c {
  font-size: 100px;
  font-weight: bold;
  color: #17a034;
}

.historyof__p {
  font-family: "Bai Jamjuree";
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 20px;
}

.header__second__p {
  font-family: "Open Sans";
  margin-bottom: 40px;
  text-align: center;
}

.header__green__button {
  color: white;
  background-color: #17a034;
  padding: 10px 15px;
  margin-right: 10px;
  letter-spacing: 2px;
  border-radius: 30px;
  box-shadow: 0px 13px 19px 5px rgba(46, 255, 128, 0.65);
  border: none;
}
.header__green__button:hover {
  cursor: pointer;
}

.header__blue__button {
  color: white;
  background-color: #3793c8;
  padding: 10px 15px;
  margin-left: 10px;
  letter-spacing: 2px;
  border-radius: 30px;
  box-shadow: 0px 13px 19px 5px #4e96c1;
  border: none;
}
.header__blue__button:hover {
  cursor: pointer;
}

.snippets__p {
  font-family: "Bai Jamjuree";
  margin-top: 90px;
  font-size: 30px;
  font-weight: bold;
}

.track__under__p {
  margin-top: 20px;
  font-family: "Open Sans";
  margin-bottom: 40px;
  text-align: center;
}

.content__main__adjust {
  display: flex;
}
.content__main__adjust .quick__container {
  margin-left: auto;
  margin-right: auto;
}

.quick__search__p {
  font-family: "Bai Jamjuree";
  font-size: 20px;
  font-weight: bold;
  margin-top: 40px;
  margin-bottom: 10px;
}

.easily__search {
  color: grey;
